<template>
	<div class="card-item" :class="{big: isBig}">
		<div class="left"></div>
		<div class="center">
			<div class="title">
				<slot name="title">{{ title }}</slot>
			</div>
			<div class="content">
				<slot name="content">{{ content }}</slot>
			</div>
		</div>
		<div class="right"></div>
	</div>
</template>
<script>
export default {
	name: 'card-item',
	props: {
		isBig: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: '经营主体数（家）'
		},
		content: {
			type: [String, Number],
			default: '17958'
		}
	}
};
</script>
<style lang="scss" scoped>
.card-item {
	display: flex;
	min-width: 196px;
	height: 85px;
	.left {
		width: 38px;
		height: 100%;
		margin-right: -1px;
		background: url('../../assets/images/common/card-left.png') no-repeat;
		background-size: 100% 100%;
	}
	.right {
		width: 38px;
		height: 100%;
		margin-left: -1px;
		background: url('../../assets/images/common/card-right.png') no-repeat;
		background-size: 100% 100%;
	}
	.center {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex-grow: 1;
		height: 100%;
		background: url('../../assets/images/common/card-center.png');
		background-size: 100% 100%;
		.title {
			width: 100%;
			height: 30px;
			box-sizing: border-box;
			line-height: 33px;
			color: #00f6ff;
			font-size: 16px;
			font-weight: bold;
			text-align: center;
			white-space: nowrap;
		}
		.content {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 50px;
			font-size: 30px;
			color: #feeb48;
		}
	}
	&.big {
		height: 115px;
		.left {
			width: 40px;
			background: url('../../assets/images/common/big-card-left.png') no-repeat;
		}
		.right {
			width: 40px;
			background: url('../../assets/images/common/big-card-right.png') no-repeat;
		}
		.center {
			background: url('../../assets/images/common/big-card-center.png');
			.content {
				height: 76px;
			}
		}
	}
}
</style>



